/* Packages */
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import styled from '@emotion/styled';

/* Services Helpers */
import {
    auth as authService,
    bookmarks as bookmarksService,
    firebase,
    storage,
} from '../../_services';

/* Mappers */
import { layoutMapper } from '../../_mappers';

/* Actions */
import { auth, layout, bookmarks } from '../../_actions';

/* Components Helpers */
import { Navbar } from '../';

/* Constants */
import { COLORS, STYLES } from '../../_constants';

/* Icons */
import { library } from '@fortawesome/fontawesome-svg-core';
import {
    faArrowCircleLeft,
    faArrowCircleUp,
    faBan,
    faBookmark,
    faBullseye,
    faCalendar,
    faComments,
    faPlay,
    faPlayCircle,
    faSearch,
    faSignInAlt,
    faSignOutAlt,
    faStar,
    faUserCircle,
    faUsers,
} from '@fortawesome/free-solid-svg-icons';

library.add(faSignInAlt);       // Auth: Login
library.add(faSignOutAlt);      // Auth: Logout
library.add(faUserCircle);      // Auth: Profile

library.add(faArrowCircleLeft); // Navigation: Back
library.add(faArrowCircleUp);   // Navigation: Scroll Up
library.add(faBullseye);        // Navigation: About

library.add(faPlay);            // Trailer: Button
library.add(faPlayCircle);      // Trailer: Button alternative

library.add(faSearch);          // Search: Button

library.add(faBan);             // Show: Age indication
library.add(faBookmark);        // Show: as 'favorite'
library.add(faCalendar);        // Show: Release Date
library.add(faComments);        // Show: Comments
library.add(faStar);            // Show: Rating
library.add(faUsers);           // Show: Cast

/* Generic Styles */
const flex = {
    display      : 'flex',
    flex         : '1 1 auto',
    flexDirection: 'column',
    position     : 'relative',
};

/* Styles */
const LayoutWrapper = styled('div')({
    ...flex,

    color          : COLORS.PRIMARY_INVERSE,
    backgroundColor: COLORS.PRIMARY,
    background     : COLORS.GRADIENTS.MAIN,

    'strong': {
        fontFamily: STYLES.FONT_FAMILY_BOLD,
        fontWeight: '500',
    },
});

/* Styles */
const LayoutContent = styled('div')({
    ...flex,
    '> div': {
        ...flex,

        '> section': {
            ...flex,
            width    : '100%',
            minHeight: '100%',
            height   : 'auto',
            position : 'absolute',
        },
    },
});

/* Component */
class Layout extends PureComponent {
    /**
     * Constructor
     *
     * @param {object} props - Component Props;
     */
    constructor(props) {
        super(props);

        this.props            = props;
        this.onResizeHandler  = this.onResizeHandler.bind(this);
        this.authHandler      = this.authHandler.bind(this);
        this.bookmarksHandler = this.bookmarksHandler.bind(this);
    }

    /**
     * Will Mount
     */
    componentWillMount() {
        this.authHandler();
        window.addEventListener('resize', this.onResizeHandler);
    }

    /**
     * Will Unmount
     */
    componentWillUmount() {
        window.removeEventListener('resize', this.onResizeHandler);
    }

    /**
     * Resize Handler
     */
    onResizeHandler() {
        const { dispatch } = this.props;

        dispatch(layout.resized());
    }

    /**
     * Authentication Handler
     */
    authHandler() {
        const { dispatch } = this.props;
        const cachedUser   = storage.get('user');

        dispatch(auth.set(cachedUser));

        firebase.getAuth.onAuthStateChanged((user) => {
            dispatch(auth.set(authService.getUser(user)));

            bookmarksService.get()
                .then((response) => {
                    dispatch(bookmarks.init(response.ids, response.list));
                })
                .catch(console.log)
            ;
        }, (error) => {
            dispatch(auth.set());
            console.error('error', error);
        });
    }

    /**
     * Bookmarks Handler
     */
    bookmarksHandler() {

    }

    /**
     * Render
     */
    render() {
        const { children } = this.props;

        return (
            <LayoutWrapper className="layout">
                <Navbar />
                <LayoutContent>
                    {children}
                </LayoutContent>
            </LayoutWrapper>
        );
    }
};

/* Exporting */
export default connect(layoutMapper)(Layout);
