/* Service Helpers */
import { firebase, storage } from './';

/* Constants Helpers */
import { AUTH } from '../_constants';

/**
 * Fill User data
 *
 * @param {object} _user
 */
const _fillUser = (_user) => {
    if (typeof _user !== 'object') {
        return _user;
    }

    let _filled = {};

    AUTH.USER_FIELDS.map((field) => {
        if (_filled[field] || !_user[field]) {
            return false;
        }

        _filled[field] = _user[field];

        return true;
    });

    return _filled;
};

/* Authentication Service */
export const auth = {
    /**
     * Get Auth reference
     */
    ref: firebase.auth,

    /**
     * Get UI Instance
     */
    ui: firebase.getUi,

    /**
     * Get Session User
     */
    getUser: (user) => {
        const _user       = (firebase.getAuth.currentUser || user);
        let _userFiltered = null;

        if (_user != null) {
            _userFiltered = _fillUser(_user);

            _user.providerData.forEach((profile) => {
                _userFiltered = _fillUser(profile);
            });
        }

        if (_userFiltered) {
            storage.set('user', _userFiltered);

        } else {
            storage.remove('user');
        }

        return _userFiltered;
    },

    /**
     * Update User data
     *
     * @param {object} data
     */
    updateUser: (data) => {
        return new Promise((resolve, reject) => {
            const user = firebase.getAuth.currentUser;

            if (!user) {
                return reject('user:not-logged');
            }

            return user.updateProfile(data);
        });
    },

    /**
     * Delete User
     */
    deleteUser: () => {
        return new Promise((resolve, reject) => {
            const user = firebase.getAuth.currentUser;

            if (!user) {
                return reject('user:not-logged');
            }

            return user.delete();
        });
    },

    /**
     * Logout
     */
    logout: () => {
        storage.clear();

        return firebase.getAuth.signOut();
    },
};
