/* Framework Helpers */
import { RADIUS } from 'aphrodite-react';

/* Constants */
import { COLORS } from '../../_constants';
const DISTANCE = 'calc(100% + 10px)';

/* Dropdown Styles */
const DROPDOWN_STYLES = {
    WRAPPER: (props) => ({
        position: 'relative',
        display : 'block',
        width   : '100%',

        WebkitBoxOrient   : 'vertical',
        WebkitBoxDirection: 'normal',

        '.dropdown': {
            '&__toggle, &__content': {
                width: '100%',
            },

            '&__toggle' : {
                display   : 'block',
                cursor    : 'pointer',
                border    : 0,
                outline   : 0,
                background: 'transparent',
            },

            '&__content': {
                position : 'absolute',
                top      : ((props.up || props.right) ? 'auto' : DISTANCE),
                bottom   : (props.up ? DISTANCE : 'auto'),
                left     : (props.right ? '100%' : (props.left) ? 'auto' : '50%'),
                right    : (props.left ? '100%' : (props.right) ? 'auto' : null),
                transform: (props.right || props.left) ? null : 'translateX(-50%)',

                display  : 'none',
                opacity  : 0,
                zIndex   : -100,
                minWidth : (props.width ? (props.width + 'px') : null),

                willChange: 'display, z-index, opacity',
                transition: 'display 0.1s linear, z-index 0.1s linear, opacity 0.25s linear',

                color     : COLORS.PRIMARY,
                background: COLORS.WHITE,

                padding: (props.thin ? 0 : '15px'),
                borderRadius: RADIUS.XS,

                fontSize: '14px',
                textAlign: 'left',

                alignSelf: 'center',

                '&.active': {
                    display: 'block',
                    opacity: 1,
                    zIndex : 10000,
                },
            },
        },

        '&.up': {
            '.dropdown': {
                '&__content': {
                    top   : 'auto',
                    bottom: '100%',
                },
            },
        },
    }),
};

/* Exporting */
export default DROPDOWN_STYLES;
