/* Constant */
import { SHOWS } from '../_constants';

/**
 * Set Search results to Context
 *
 * @param {string} term
 * @param {object} content
 *
 * @return {function} dispatch
 */
const search = (term = '', content = null) => {
    return dispatch => {
        dispatch({
            type   : SHOWS.SEARCH,
            term   : term,
            content: content,
        });
    };
};

/**
 * Set Search loading to Context
 *
 * @param {object} content
 *
 * @return {function} dispatch
 */
const searchLoading = (content) => {
    return dispatch => {
        dispatch({
            type   : SHOWS.SEARCH_LOADING,
            content: content,
        });
    };
};

/* Exporting */
export const shows = {
    search,
    searchLoading,
};
