/* Packages */
import React from 'react';
import styled from '@emotion/styled';

/* Framework Helpers */
import { MEDIA_QUERIES } from 'aphrodite-react';

/* Wrapper Styles */
const RowWrapper = styled('div')((props) => ({
    display      : 'flex',
    flex         : '0 1 auto',
    flexDirection: 'row',
    flexWrap     : 'wrap',

    marginRight: (props.stretched ? '-20px' : '-10px'),
    marginLeft : (props.stretched ? '-20px' : '-10px'),

    [MEDIA_QUERIES.LT.SM]: {
        marginRight: (props.stretched ? '-15px' : null),
        marginLeft : (props.stretched ? '-15px' : null),
    },

    ...props.styles,
}));

/* Component */
const Row = (props) => {
    const { children, stretched, styles } = props;
    return (
        <RowWrapper stretched={stretched} styles={styles}>
            {children}
        </RowWrapper>
    );
};

/* Exporting */
export default Row;
