/* Packages */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavLink, withRouter } from 'react-router-dom';
import { animateScroll as scroll } from 'react-scroll';
import ReactImg from 'react-image';

/* Framework Helpers */
import { Placeholder } from 'aphrodite-react';

/* Helper Components */
import { BookmarkButton, Title } from '../';

/* Mappers */
import { showsMapper } from '../../_mappers';

/* Actions */
import { shows } from '../../_actions';

/* Constants */
import { ROUTES } from '../../_constants';
import SHOW_STYLES from './show.styles';
const { BOOKMARK_BTN, CARD_MAX_SUMMARY, CardWrapperStyled } = SHOW_STYLES;

/* Component */
class ShowCard extends Component {
    /**
     * Constructor
     *
     * @param {object} props
     */
    constructor(props) {
        super(props);

        this.props = props;
        this.state = {
            loading : false,
            selected: false,
        };

        this.onClickHandler = this.onClickHandler.bind(this);
    }

    /**
     * Click Handler
     */
    onClickHandler(evt) {
        const { ctrlKey, metaKey } = evt;
        const { loading } = this.state;

        if (ctrlKey || metaKey) {
            return;
        }

        evt.preventDefault();

        this.setState({
            selected: true,
        });

        if (loading) {
            return;
        }

        this.setState({
            loading: true,
        });

        scroll.scrollTo(0, {
            duration: 450,
        });

        setTimeout(() => {
            const { dispatch, history } = this.props;

            history.push(ROUTES.SHOW.replace(':id', this.props.show.id));
            dispatch(shows.search('', null));
            dispatch(shows.searchLoading(false));
        }, 450);
    }

    /**
     * Render
     */
    render() {
        const { selected } = this.state;
        const { show, loading, mobile } = this.props;
        const {
            id,
            name,
            image,
            // genres,
            summary,
        } = (show || {});
        const poster = image ? (image.original || image.medium) : '';
        let _summary =
            summary ?
                summary.substring(0, CARD_MAX_SUMMARY)
                :
                ''
        ;
        _summary = _summary.concat(_summary.length === CARD_MAX_SUMMARY ? '...' : '');

        if (loading || !show || !id) {
            return (
                <Placeholder />
            );
        }

        return (
            <CardWrapperStyled
                mobile={mobile}
                className={`show-card ${(this.state.loading || selected) ? 'show-card--selected' : ''}`}>
                <BookmarkButton id={id} show={show} styles={BOOKMARK_BTN} />
                <NavLink
                    className="show-card__link"
                    onClick={this.onClickHandler}
                    to={ROUTES.SHOW.replace(':id', id)}>
                    <ReactImg
                        src={poster}
                        className="show-card__poster"
                    />
                    <Title type="h4" className="show-card__title">
                        {name}
                    </Title>
                    <div dangerouslySetInnerHTML={{ __html: _summary }}></div>
                </NavLink>
            </CardWrapperStyled>
        );
    }
};

/* Exporting */
export default connect(showsMapper)(withRouter(ShowCard));
