/* Packages */
import React from 'react';
import styled from '@emotion/styled';

/* Wrapper Styles */
const ColumnWrapper = styled('div')((props) => ({
    display  : 'flex',
    flex     : '0 0 auto',
    flexGrow : 1,
    flexBasis: 0,
    maxWidth : '100%',

    paddingRight: '10px',
    paddingLeft : '10px',

    ...props.styles,
}));

/* Component */
const Column = (props) => {
    const { children, styles } = props;
    return (
        <ColumnWrapper styles={styles}>
            {children}
        </ColumnWrapper>
    );
};

/* Exporting */
export default Column;
