/* Services Helpers */
import axios from 'axios';
import { storage } from './';

/* Constants */
import { API } from '../_constants';
const { URL, ENDPOINTS } = API;
const { SEARCH, SHOW }   = ENDPOINTS;
const _STORAGE_PREFIX    = 'show_';

/* Shows Service */
export const shows = {
    /**
     * Search
     *
     * @param {string} term
     *
     * @return {Promise}
     */
    search: (term = '') => {
        return new Promise((resolve, reject) => {
            axios
            .get(URL.concat(SEARCH.replace(':term', term)))
            .then((response) => {
                const { data } = (response || {});

                resolve(data || []);
            })
            .catch((error) => {
                reject(error);
            });
        });
    },

    /**
     * Get TV Show by ID
     *
     * @param {any} id
     *
     * @return {Promise}
     */
    getById: (id) => {
        const STORAGE_KEY = _STORAGE_PREFIX.concat(id);

        return new Promise((resolve, reject) => {
            const cached = storage.get(STORAGE_KEY);

            if (cached) {
                resolve(cached);
            }

            axios
            .get(URL.concat(SHOW.replace(':id', id)))
            .then((response) => {
                const { data } = (response || {});

                resolve(data || null);

                if (!data) {
                    return;
                }

                storage.set(STORAGE_KEY, Object.assign({}, cached, data));
            })
            .catch((error) => {
                reject(error);
            });
        });
    },
};
