/* Packages */
import React from 'react';
import { connect } from 'react-redux';
import styled from '@emotion/styled';

/* Mappers */
import { genericMapper } from '../../_mappers';

/* Helper Components */
import { Dropdown, Login } from '../';

/* Constants */
import NAVBAR_STYLES from './navbar.styles';
const DropdownItemStyled = styled('div')(Object.assign({}, NAVBAR_STYLES.MENU_USER, { paddingRight: 0, paddingLeft: 0 }));

/* Component */
const NavbarMenuAuth = (props) => {
    const { auth, toggle, up } = props;
    const { authenticated }    = auth;

    if (authenticated) {
        return (null);
    }

    return (
        <Dropdown
            thin
            up={up}
            width={240}
            toggle={toggle}>
            <DropdownItemStyled>
                You must need to
                <br />
                <strong>authorize browser Pop-Ups</strong>
                <br />
                to proceed.
            </DropdownItemStyled>
            <DropdownItemStyled>
                <Login />
            </DropdownItemStyled>
        </Dropdown>
    );
};

/* Exporting */
export default connect(genericMapper)(NavbarMenuAuth);
