/* Packages */
import React from 'react';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import styled from '@emotion/styled';

/* Redux Mappers */
import { layoutMapper } from '../../_mappers';

/* Constants */
import { ROUTES } from '../../_constants';

/* Framework Helpers */
import {
    Container,
    MEDIA_QUERIES,
} from 'aphrodite-react';

/* Components Helpers */
import { Logo, Row, Column, Search } from '../';
import NavbarMenu from './NavbarMenu';

/* Wrapper Styles */
const NavbarWrapper = styled('nav')((props) => ({
    padding  : '15px 0',
    textAlign: 'left',

    [MEDIA_QUERIES.LT.SM]: {
        padding: '10px 0',
    },
}));

/* Component */
const Navbar = (props) => {
    const { history, xs } = props;
    const { location }    = history;
    const { pathname }    = location;
    const logoWidth       = (!xs ? '295px' : '100%');
    const isOnSearchPage  = (pathname === ROUTES.SEARCH_INDEX);

    return (
        <NavbarWrapper>
            <Container>
                <Row>
                    <Column styles={{ maxWidth: logoWidth, minWidth: logoWidth }}>
                        <Link style={{ display: 'block', width: (!xs ? '100%' : '50px'), margin: (!xs ? 0 : '0 auto') }}
                              to={ROUTES.INDEX}>
                            <Logo title={true} />
                        </Link>
                    </Column>
                    {(xs) ? (null) : (
                        <Column>
                            {(isOnSearchPage) ? (null) : (
                                <Search />
                            )}
                        </Column>
                    )}
                    {(xs) ? (null) : (
                        <Column>
                            <NavbarMenu />
                        </Column>
                    )}
                </Row>
            </Container>
            {(!xs) ? (null) : (<NavbarMenu />)}
        </NavbarWrapper>
    );
};

/* Exporting */
export default connect(layoutMapper)(withRouter(Navbar));
